body#game-body {
  margin: 0;
  padding: 0;
  color: #333;
  font-family: arial, sans-serif;
  font-size: 14px; }
  body#game-body #game-container {
    min-height: 100vh;
    background-color: blue;
    position: relative; }
    body#game-body #game-container .ribbon {
      position: absolute;
      top: 0;
      left: 0;
      background-color: rgba(51, 51, 51, 0.5);
      width: 100%;
      font-size: 0.5rem;
      display: flex;
      flex-direction: row;
      justify-content: space-between;
      align-items: center; }
      body#game-body #game-container .ribbon .game-title {
        margin: 0;
        padding: 0;
        padding: 0.5em;
        color: #eee;
        text-shadow: 1px 1px 1px #333;
        box-sizing: border-box; }
      body#game-body #game-container .ribbon #info {
        color: #eee;
        font-size: 14px;
        box-sizing: border-box;
        margin: 0;
        padding: 0;
        padding: 0.2em; }
      body#game-body #game-container .ribbon .wind {
        width: 100%;
        height: 100%;
        background-image: linear-gradient(90deg, #ff0a0a 25%, #eeeeee 25%, #eeeeee 50%, #ff0a0a 50%, #ff0a0a 75%, #eeeeee 75%, #eeeeee 100%);
        background-size: 10px;
        clip-path: polygon(0% 0%, 0% 100%, 100% 50%);
        transition: clip-path 0.5s; }
        body#game-body #game-container .ribbon .wind.right {
          clip-path: polygon(50% 0%, 50% 100%, 100% 50%); }
          body#game-body #game-container .ribbon .wind.right.half {
            clip-path: polygon(50% 0%, 50% 100%, 75% 50%); }
        body#game-body #game-container .ribbon .wind.left {
          clip-path: polygon(0% 50%, 50% 0%, 50% 100%); }
          body#game-body #game-container .ribbon .wind.left.half {
            clip-path: polygon(25% 50%, 50% 0%, 50% 100%); }
      body#game-body #game-container .ribbon .wind-container {
        height: 20px;
        width: 100px;
        background-color: rgba(39, 184, 241, 0.5);
        display: flex;
        flex-direction: row;
        justify-content: center;
        align-items: center; }
